@media (max-width: $mobile-width) {
  .page-footer {
    width: auto;
    // margin: 0 auto;
    padding-left: 17px;
    padding-right: 17px;

  }

  .page-footer__wrapper {
    flex-wrap: wrap;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .page-footer__logo {
    margin-top: 27px;
  }

  .page-footer__copyright {
    margin-top: 25px;
    margin-left: 0px;
    margin-right: 0;
  }

  .page-footer__social {
    margin-top: 33px;
    margin-left: 0;
  }

  .page-footer__list {
    display: none;
  }

  .page-footer__nav-wrapper {
    align-self: flex-start;
    flex-direction: column;
    margin-top: 54px;
    margin-bottom: 51px;
  }

  .page-footer__sections {
    position: relative;
    padding-bottom: 29px;
    padding-left: 0;
    padding-top: 29px;
    border-top: 1px solid rgba(#626962, 0.2);
    border-bottom: 1px solid rgba(#626962, 0.2);
