  }

  .page-footer__info {
    flex-direction: column;
    align-self: center;
  }

  .page-footer__studio {
    margin-left: 0;
  }

  .page-footer__line {
  margin-top: -108px;
  }

  .page-footer__adress {
    padding-top: 25px;
  }

  .page-footer__adress p,
  .page-footer__list {
    width: 246px;
  }

  .page-footer__adress-name {
    position: relative;
    padding-bottom: 32px;
