.about-us {
  display: flex;
  flex-wrap: nowrap;
  box-sizing: border-box;
  padding: 73px 7% 71px 4.7%;
  box-sizing: border-box;
  max-width: 1411px;
  margin: 0 auto;
  flex-grow: 1;
}

.about-us__numbers {
  width: 220px;
  height: 317px;
  background-image: url("../img/numbers-desktop@1x.png");
  background-size:  220px 317px;
  background-repeat: no-repeat;
  background-position: 0 5px;
  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    background-image: url("../img/numbers-desktop@2x.png");
  }
}

.about-us__container {
  display: flex;
  flex-direction: column;
  width: 84%;
  margin-left: -5px;
}

.about-us h2 {
  margin: 0;
  margin-bottom: 21px;
  font-family: 'RobotoCondensed-Regular', 'Arial', monospace;
  font-style: normal;
  font-weight: normal;
  font-size: 36px;
  line-height: 51px;
  color: #666666;
}

.about-us p {
  margin: 0;
  font-family: 'ptmono', 'Arial', monospace;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 22px;
  color: #666666;
  margin-bottom: 22px;
  &:nth-of-type(2) {
    margin-bottom: 0;
  }
  &:last-of-type {
    margin-bottom: 36px;
  }
}

.about-us__more {
  margin-right: auto;
  margin-top: 0;
  font-family: 'Roboto Condensed', 'Arial', monospace;
  font-style: normal;
  font-weight: bold;
  font-size: 14px;
  line-height: 16px;
  text-align: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  text-decoration: none;
  padding: 14px 41px 13px 47px;
  border: 1px solid $blue;
  border-radius: 5px;
  color: $blue;
}

@media (max-width: $tablet-width) {
  .about-us {
    display: flex;
    flex-wrap: nowrap;
    width: 100%;
    max-width: 950px;
    padding-left: 25px;
    padding-right: 25px;
    padding-top: 25px;
  }

  .about-us h2 {
    font-size: 32px;
    line-height: 45px;
    margin-bottom: 25px;
  }

  .about-us p {
    &:nth-of-type(2) {
      margin-bottom: 39px;
    }
    &:last-of-type {
      display: none;
    }
  }

  .about-us__container {
    display: flex;
    flex-direction: column;
    width: 84%;
    margin-left: -28px;
}

  .about-us__numbers {
    width: 244px;
    height: 362px;
    background-image: url("../img/numbers-tablet@1x.png");
    background-size:  244px 362px;
    background-repeat: no-repeat;
    background-position: -25px 0;
    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background-image: url("../img/numbers-tablet@2x.png");
    }
}

  .about-us__more {
    margin-bottom: 13px;
  }
}

@media (max-width: $mobile-width) {
  .about-us {
    width: auto;
    padding-top: 39px;
    padding-left: 16px;
    padding-right: 5px;
    padding-bottom: 71px;
  }

  .about-us__numbers {
    display: none;
  }

  .about-us h2 {
    font-size: 28px;
    line-height: 39px;
    margin-bottom: 21px;
  }

  .about-us p {
    font-size: 15px;
    line-height: 22px;
    width: 100%;
    margin-bottom: 39px;
    &:nth-of-type(2) {
      margin-bottom: 28px;
    }
  }

  .about-us__more {
    margin-top: 23px;
  }

  .about-us__container {
    width: 100%;
    margin: 0;
    padding: 0;
  }
}
